@import url('https://fonts.googleapis.com/css2?family=Doodle+Shadow&display=swap');

.about-container {
  display: flex;
  align-items: flex-end;
  gap: 3rem;
  padding: 5rem 2rem;
  min-height: 100vh;
  padding-right:600px;
}

.vertical-text {
  left: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 1000;
  font-family: 'Rubik Doodle Shadow', cursive;
  font-size: 4rem;
  color: #040941;
}

.vertical-text .letter {
  margin: 0.2rem 0;
  transition: transform 0.3s ease-in-out;
}

.vertical-text .letter:hover {
  transform: scale(1.2) rotate(5deg);
}

.vertical-text .space {
  margin-bottom: 1rem;
}


/* AboutUs.css */
.about-content {
  background-color: #dda5ec; /* pastel purple like in your image */
  border-radius: 40px;
  padding: 60px;
  max-width: 700px;
  text-align: center;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  position: relative;
  margin-left: 50px;
  margin-right: 100px;
  margin-inline-start: -100px;
  margin-top: 50px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-style: italic;
  color: #040941; /* dark purple for text */
  transition: transform 0.3s ease-in-out;
}

/* Optional arrows for the bubble shape */
.about-content::before,
.about-content::after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border-style: solid;
}

.about-content::before {
  left: -30px;
  border-width: 25px 30px 25px 0;
  border-color: transparent #7b2cbf transparent transparent;
}

.about-content::after {
  right: -30px;
  border-width: 25px 0 25px 30px;
  border-color: transparent transparent transparent #7b2cbf;
}

/* Optional: Add arrow styles on both sides */
.about-content::before,
.about-content::after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: 20px solid transparent;
}

.about-content::before {
  left: -40px;
  border-right-color: #7b2cbf; /* purple arrow */
}

.about-content::after {
  right: -40px;
  border-left-color: #7b2cbf;
}
